import React from 'react';
import './DashBoard.css';
import '../../../static/Grid.css';
import Button from '@material-ui/core/Button';
import ls from 'local-storage';
import axios from 'axios';
import stringify from 'qs-stringify';
import UploadItem from '../uploadItem/uploadItem';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {ItemMobile} from '../items/Item';













function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}



function UserProfile(props) {
  return( <div className='Profile'>
                <div className='Name'> {props.userData.name}</div>
                <div className='Email'> {props.userData.email}</div>
                <div className='Address'> {props.userData.address}</div>
                <div className='City'> {props.userData.city}</div>
                <div className='State'>{props.userData.state} </div>
                <div className='phone'> {props.userData.phoneno}</div>
                </div>)
}


function UploadedItem(props) {
  return(
    <div className='uploadedItem'>
      <Paper variant='outlined'>
        <div className='uploadedItemBody'>
        <div className='uploadedItemImg'>
        </div>
          <div className='uploadedItemInfo'>
        <div className='uploadedItemName'>{props.item.title}</div>
         <div>
          <div className='uploadedItemRegNo'>
       
            <span class="registration-ui"> {props.item.regno.slice(0, 2).toUpperCase()+ " "} {props.item.regno.slice(2).toUpperCase()}</span>

         
           
           
         
          
          </div>
         </div>
        
          </div>
          <div className='uploadedItemBrand'>
          {props.item.brand}
          </div>
          
          <div className='uploadedItemPrice'>
          ₹ {props.item.price}
          </div>
          <div className='uploadedItemAddress'>
           {props.item.address}
          </div>
          <div style={{'display' : 'flex', 'flex-direction' : 'row'}}>
          <div className='uploadedItemCity'>
           {props.item.city}
          </div>
          <div className='uploadedItemState'>
          {props.item.state}
          </div>
          </div>
          <div className='uploadedItemAvailable'>
          </div>
          <div className='uploadedItemDescription'>
          </div>
          <div className='uploadedItemDate'>
          </div>

        </div>
      </Paper>
    </div>
    )
}

function YourUploads(props) {
  return(<div className='YourUploads'>
        { props.items.map((item)=>{
            return <UploadedItem item={item}/>

        })}
          
          </div>)
}

function YourOrder(props) {
  return(<div className='Order'>
            Current Order
          </div>)
}

function OrderHistory(props) {
  return(<div className='OrderHistory'>
            Order History
          </div>)
}

function YourEarnings(props) {
  return(<div className='YourEarnings'>
            YourEarnings
          </div>)
}

function GetUserData(that){
	        let user = {  username : ls.get('username'),
			  uniqueid : ls.get('uniqueid'),
			  ssto : ls.get('ssto'),
	          tokenId: ls.get('tokenId'),
	          t2 : ls.get('t2'),
				}

			 axios.post(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php`,
      stringify({ fetchUserDetail:'',
                  tokenId : user.tokenId,
                  t2 : user.t2,
                  username : user.username,
                  uniqueid : user.uniqueid,
                  ssto : user.ssto

                   }))
    .then((response) =>{
     
      if(response.data.success){
       console.log(response.data.details[0]);
        /* After Validation Code START */
        that.setState({
        			'name' : response.data.details[0].name,
        			'username' : response.data.details[0].username,
        			'email' : response.data.details[0].email,
        			'address' : response.data.details[0].address,
        			'city' : response.data.details[0].city,
        			'state' : response.data.details[0].state,
        			'phoneno' : response.data.details[0].phoneno,


        })
         ls.set('city',that.state.city);
  		ls.set('state',that.state.state);
  		ls.set('address', that.state.address);
 		ls.set('phoneno', that.state.phoneno);
        if(!response.data.uploads.empty)
        	that.setState({'uploads' : response.data.uploads});

           }
          else {
        console.log("user not logged in");
          /* After Validation Code END */
        
          }
  });

}




class DashBoard extends React.Component {

	constructor(props){
		super(props);
		this.state = {
		username : '',
		name : '',
		email : '',
		address : '',
		city : '',
		state : '',
		phoneno : '',
		uploads : [],
    value : 0      
		}
		
		
	}

componentDidMount() {
		GetUserData(this);
}

handleChange(event, newValue){
    this.setState({'value' : newValue});
  };

handleChangeIndex(index){
    this.setState({'value' : index});
  };


	render(){

		return(
				<div className='DashBoard'>
            
          <div>
      <Paper variant='outlined'>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange.bind(this)}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          centered
          aria-label="full width tabs example"
        >
          <Tab label="Uploads" {...a11yProps(0)} />
          <Tab label="Orders" {...a11yProps(1)} />
          <Tab label="History" {...a11yProps(2)} />
           <Tab label="Earning" {...a11yProps(3)} />
          <Tab label="Settings" {...a11yProps(4)} />
        </Tabs>
      </Paper>
      <SwipeableViews
        
        index={this.state.value}
        onChangeIndex={this.handleChangeIndex.bind(this)}
      >

      <TabPanel value={this.state.value} index={0}>
       <YourUploads items={this.state.uploads}/>
          
        </TabPanel>

        <TabPanel value={this.state.value} index={1}>
        <YourOrder/>
          
        </TabPanel>
        <TabPanel value={this.state.value} index={2} >
          <OrderHistory/>
        </TabPanel>
        <TabPanel value={this.state.value} index={3} >
          <YourEarnings/>
        </TabPanel>
         <TabPanel value={this.state.value} index={4} >
          <UserProfile userData={this.state}/>
        </TabPanel>
      </SwipeableViews>
    </div>
				</div>
			)
	}
}






export default DashBoard;